import React, { useState } from "react";
import { Link } from "react-router-dom";
import Main from "../Main/Main.js";
import Signup from "../Signup/Signup.js";

import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { styled } from '@mui/system';

/*
  * 1. FileName : Background
  * 2. Comment   :   
  * 3.     : boreum
  * 4.     : 2024. 06. 26
*/

/*
  * 1. FunctionName: CustomTextField
  * 2. Comment   : TextField  
  * 3.     : boreum
  * 4.     : 2024. 06. 27
*/
const CustomTextField = styled(TextField)(({ theme }) => ({
    width: '370px',
    '& .MuiInputBase-input': {
        color: 'white',
      },
    '& .MuiInputLabel-root': {
        color: '#B6B6B6',
    },
    '& .MuiInputLabel-root.Mui-focused': {
        color: 'white',
    },
    '& .MuiOutlinedInput-root': {
        backgroundColor: '#323D4E',
        opacity: 1,
        height: '52px',
        '& fieldset': {
            borderColor: '#CFCFCF1D',
            borderWidth: '0.2px',
        },
        '&:hover fieldset': {
            borderColor: '#CFCFCF1D',
            borderWidth: '0.2px',
        },
        '&.Mui-focused fieldset': {
            borderColor: '#FFFFFF1D',
            borderWidth: '2px',
        },
    },
}));

/*
  * 1. FunctionName: Login
  * 2. Comment   :   ,   
  * 3.     : boreum
  * 4.     : 2024. 06. 27
*/
const Login = ({ marginBottom, onSignupClick }) => {
    //   , 
    const [loginInput, setLoginInput] = useState({
        userId: "",
        password: "",
    });

    //    ,   
    const handleInputChange = (e) => {
        setLoginInput((prev) => ({
            ...prev,
            [e.target.id]: e.target.value
         }));
    };

    //   
    const activeEnter = (e) => {
        if(e.key === "Enter") {
            onLogin(e);
        }
    }

    // 
    const [userIdError, setUserIdError] = useState("");
    const [passwordError, setPasswordError] = useState("");

    //    
    const idStyles = {
        marginBottom: userIdError ? '0px' : '24px'
    };
    const pwStyles = {
        marginBottom: passwordError ? '0px' : '24px'
    };

    //   : Main.js
    const onDashboard = (e) => {
        onSignupClick();
    }

    //  
	const onLogin = async (e) => {
        e.preventDefault();

        //  
        let isValid = true;

        if (loginInput.userId.trim() === "") {
            setUserIdError(" .");
            isValid = false;
        } else {
            setUserIdError("");
        }

        if (loginInput.password.trim() === "") {
            setPasswordError(" .");
            isValid = false;
        } else {
            setPasswordError("");
        }

        if(isValid) {
            //      :    
            onSignupClick();

            // 
    /*        try {
                const res = await axiosInstance.post(`/com/login`, {
                    userId: loginInput.userId,
                    password: loginInput.password,
                });
                //   
                if (res.status === 200) {
                    console.log(res.data);
                    onSignupClick();
                }
            } catch (error) {
                console.log(error);
                isValid = false;
                setPasswordError("   .");
                setLoginInput((prev) => ({
                    ...prev,
                    password: ""
                }));
            }*/
        }
    };

    return (
        <Stack
            component="form"
            sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                width: '25ch',
                padding: '20px',
                minHeight: '100vh',
                margin: 'auto',
                minWidth: '400px',
            }}
            spacing={3}
            noValidate
            autoComplete="off"
        >
            <CustomTextField
                label=""
                id="userId"
                value={loginInput.userId}
                onChange={handleInputChange}
                inputProps={{ maxLength: 30, }}
                size="small"
                required
                error={!!userIdError}
                helperText={userIdError}
                style={idStyles}
            />
            <CustomTextField
                label=""
                id="password"
                type="password"
                value={loginInput.password}
                onChange={handleInputChange}
                onKeyDown={(e) => activeEnter(e)}
                inputProps={{ maxLength: 30, }}
                size="small"
                required
                error={!!passwordError}
                helperText={passwordError}
                style={pwStyles}
            />
            <div
                style={{
                    display: 'flex',
                    justifyContent: 'flex-end',
                    width: '100%',
                    marginTop: '0px',
                    fontSize: '12px',
                }}
            >
                <Button
                    variant="text"
                    onClick={onDashboard}
                    style={{
                        color: "#B6B6B6",
                    }}
                >
                    
                </Button>
            </div>
            <Button
                variant="contained"
                onClick={onLogin}
                style={{
                    marginTop: "40px",
                    minWidth: "280px",
                }}
            >
                
            </Button>
        </Stack>
    );
}

export default Login;