import React, { useState } from 'react';
import { Button, TextField, Typography, Container, Paper, Box, CircularProgress, InputAdornment, Select, MenuItem } from '@mui/material';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ErrorIcon from '@mui/icons-material/Error';


function Signup() {
    const [username, setUsername] = useState('');
    const [usernameError, setUsernameError] = useState(false);
    const [usernameHelperText, setUsernameHelperText] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [passwordError, setPasswordError] = useState(false);
    const [passwordHelperText, setPasswordHelperText] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [confirmPasswordError, setConfirmPasswordError] = useState(false);
    const [confirmPasswordHelperText, setConfirmPasswordHelperText] = useState('');
    const [phone, setPhone] = useState('');
    const [phoneError, setPhoneError] = useState(false);
    const [phoneHelperText, setPhoneHelperText] = useState('');
    const [category, setCategory] = useState('');

    const handleSignUp = (event) => {
        event.preventDefault();
        //   
        console.log(username, email, password, confirmPassword, phone);
    };

    const isValidUsername = (username) => {
        //  username    
        //  2  30 ,  3  30 
        const length = username.length;
        const hasKorean = /[-]/.test(username);
        const hasEnglish = /^[A-Za-z]+$/.test(username);
        const noSpecialChars = /^[-A-Za-z0-9]+$/.test(username);
        
        if (length > 30 || !noSpecialChars) return false;
        if (hasKorean && length >= 2) return true;
        if (hasEnglish && length >= 3) return true;
        return false;
    };

    const handleUsernameChange = (e) => {
        const newUsername = e.target.value;
        setUsername(newUsername);
        if (!isValidUsername(newUsername)) {
            setUsernameError(true);
            setUsernameHelperText(' 2 ,  3 ,  30  .');
        } else {
            setUsernameError(false);
            setUsernameHelperText('');
        }
    };

    const isValidPassword = (password) => {
        const hasLetters = /[a-zA-Z]/.test(password);
        const hasNumbers = /\d/.test(password);
        const hasSpecialChars = /[!@#$%^&*(),.?":{}|<>]/.test(password);
        const validLength = password.length >= 10;
        const validShortLength = password.length >= 8;
        
        const typeCount = [hasLetters, hasNumbers, hasSpecialChars].filter(Boolean).length;
        
        if ((typeCount >= 2 && validLength) || (typeCount === 3 && validShortLength)) {
            return true;
        } else {
            return false;
        }
    };

    const handlePasswordChange = (e) => {
        const newPass = e.target.value;
        setPassword(newPass);
        if (newPass.length > 0) {
            if (isValidPassword(newPass)) {
                setPasswordError(false);
                setPasswordHelperText('');
            } else {
                setPasswordError(true);
                setPasswordHelperText('   ');
            }
        }
        // Confirm password validation on password change
        if (confirmPassword.length > 0) {
            if (newPass !== confirmPassword) {
                setConfirmPasswordError(true);
                setConfirmPasswordHelperText('  ');
            } else {
                setConfirmPasswordError(false);
                setConfirmPasswordHelperText('');
            }
        }
    };

    const handleConfirmPasswordChange = (e) => {
        const newConfirmPass = e.target.value;
        setConfirmPassword(newConfirmPass);
        if (newConfirmPass !== password) {
            setConfirmPasswordError(true);
            setConfirmPasswordHelperText('  ');
        } else {
            setConfirmPasswordError(false);
            setConfirmPasswordHelperText('');
        }
    };

    const isValidPhone = (phone) => {
        // :     (010-xxxx-xxxx)
        return /^010-\d{4}-\d{4}$/.test(phone);
    };

    const handlePhoneChange = (e) => {
        const newPhone = e.target.value;
        setPhone(newPhone);
        if (isValidPhone(newPhone)) {
            setPhoneError(false);
            setPhoneHelperText('');
        } else {
            setPhoneError(true);
            setPhoneHelperText('   ');
        }
    };

    return (
        <Container component="main" maxWidth="xs">
            <Paper elevation={6} sx={{ mt: 8, padding: 3 }}>
                <Typography component="h1" variant="h5">
                    
                </Typography>
                <Box component="form" onSubmit={handleSignUp} sx={{ mt: 1 }}>
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        id="username"
                        label=""
                        name="Username"
                        autoFocus
                        value={username}
                        onChange={handleUsernameChange}
                        error={usernameError}
                        helperText={usernameHelperText}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    {!usernameError && username && isValidUsername(username) ? <CheckCircleIcon style={{ color: 'green' }} /> : null}
                                </InputAdornment>
                            )
                        }}
                    />
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        id="email"
                        label=""
                        name="email"
                        autoComplete="email"
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                    />
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label=""
                        type="password"
                        id="password"
                        value={password}
                        onChange={handlePasswordChange}
                        error={passwordError}
                        helperText={passwordHelperText}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    {password.length > 0 && !passwordError ? <CheckCircleIcon style={{ color: 'green' }} /> :
                                    passwordError ? <ErrorIcon style={{ color: 'red' }} /> : null}
                                </InputAdornment>
                            )
                        }}
                    />
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        name="confirmPassword"
                        label=" "
                        type="password"
                        id="confirmPassword"
                        value={confirmPassword}
                        onChange={handleConfirmPasswordChange}
                        error={confirmPasswordError}
                        helperText={confirmPasswordHelperText}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    {!confirmPasswordError && confirmPassword ? <CheckCircleIcon style={{ color: 'green' }} /> :
                                    confirmPasswordError ? <ErrorIcon style={{ color: 'red' }} /> : null}
                                </InputAdornment>
                            )
                        }}
                    />
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        id="phone"
                        label=""
                        name="phone"
                        value={phone}
                        onChange={handlePhoneChange}
                        error={phoneError}
                        helperText={phoneHelperText}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    {!phoneError && phone ? <CheckCircleIcon style={{ color: 'green' }} /> : null}
                                </InputAdornment>
                            )
                        }}
                    />
                    <TextField
                        margin="normal"
                        fullWidth
                        select
                        label=""
                        value={category}
                        onChange={(e) => setCategory(e.target.value)}
                        helperText="  "
                    >
                        <MenuItem value="director"></MenuItem>
                        <MenuItem value="host"> </MenuItem>
                    </TextField>
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 3, mb: 2 }}
                    >
                        
                    </Button>
                </Box>
            </Paper>
        </Container>
    );
}

export default Signup;
