import React, { useState } from 'react';
import { Drawer, List, ListItem, ListItemText, IconButton, Box, Typography, CssBaseline, Container } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
//    
// ) import Header from "../Test_seungwon/Test_seungwon.js";
import Login from '../Login/Login.js';
import Signup from '../Signup/Signup.js';

/*
  * 1. MethodName: Background
  * 2. ClassName : Background
  * 3. Comment   : (Drawer), ,   
  * 4.     : 
  * 5.     : 2024. 06. 27
*/
function Background() {
  const [isOpen, setIsOpen] = useState(false);
  const [currentComponent, setCurrentComponent] = useState('login'); //      

  const handleSignupClick = () => {
    setCurrentComponent('signup');
  };

  const handleLoginClick = () => {
    setCurrentComponent('login');
  };

  // Drawer   
  const toggleDrawer = (open) => (event) => {
    // Tab Shift   
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setIsOpen(open);
  };

  // Drawer   
  const drawerList = () => (
    <Box
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
        {['', ' '].map((text) => (
          <ListItem button key={text}
             sx={{ 
              '&:hover': {
              bgcolor: '#4880FF',
            }
          }}>
            <ListItemText 
              primary={text} 
              primaryTypographyProps={{
                sx: {
                  color: 'white',
                  paddingLeft: 3,
                  paddingTop: 1,
                  fontSize: '1.1rem', 
                  fontWeight: 600 
                }
              }} 
            />
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{
      width: '100%',
      minHeight: '100vh', //    100% 
      bgcolor: '#273142',
      color: 'white', 
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center'
    }}>
      {/*   */}
      <Box sx={{
        width: '100%',
        height: 70,
        bgcolor: '#313D4F', 
        color: 'white',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start', 
        paddingLeft: 2, 
      }}>
        {/*    */}
        <IconButton onClick={toggleDrawer(true)} sx={{ color: 'white', marginRight: 2 }}>
          <MenuIcon />
        </IconButton>
        {/*   */}
        <Typography variant="h5"
          sx={{ 
            fontWeight: 500 
          }}
        >
          MilGam
        </Typography>
      </Box>
      {/* Drawer  */}
      <Drawer
        anchor='left'
        open={isOpen}
        onClose={toggleDrawer(false)}
        PaperProps={{
          sx: {
            bgcolor: '#313D4F', 
            color: 'white', 
            width: '12%'
          }
        }}
      >
        {drawerList()}
      </Drawer>
      {/*     */}
      <Typography variant="h6" 
        sx={{ 
          color: 'white', 
          marginTop: '35px', 
          alignSelf: 'flex-start', 
          marginLeft: '3%', 
          fontSize: '1.5rem', 
          fontWeight: 700 
        }}
      >
        {currentComponent === 'login' ? '' : ''}
      </Typography>
      {/* SimpleContainer  */}
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth={false} sx={{ width: '95%', bgcolor: '#313D4F', borderRadius: '12px', padding: '16px', marginTop: '10px', marginBottom: '50px' }}>
          {currentComponent === 'login' ? (
            <Login onSignupClick={handleSignupClick} />
          ) : (
            <Signup onLoginClick={handleLoginClick} />
          )}
        </Container>
      </React.Fragment>
    </Box>
  );
}

export default Background;